// ---APPLICABLE---

.applicable {
  display: flex;
  justify-content: center;
  min-height: 710px;
  width: 100%;
  background-color: #F9F9F9;
  margin-bottom: 98px;
}

@media (max-width: $tablet) {
  .applicable {
    flex-direction: column;
    margin-bottom: 78px;
  }
}

@media (max-width: $mobile) {
  .applicable {
    margin-bottom: 41px;
  }
}

.applicable__container_col {
  display: flex;
  align-items: center;
  width: 50%;

  &:first-child {
    justify-content: flex-end;
  }

  &:last-child {
    justify-content: flex-start;
  }
}

@media (max-width: $tablet) {
  .applicable__container_col {
    width: 100%;
    justify-content: center;
    margin-bottom: 81px;

    &:first-child {
      order: 1;
      justify-content: center;
    }

    &:last-child {
      justify-content: center;
      margin-bottom: 68px;
    }
  }
}

@media (max-width: $mobile) {
  .applicable__container_col {
    margin-bottom: 41px;

    &:first-child {
      margin-bottom: 65px;
    }
  }
}

.applicable__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 550px;
  margin-left: 50px;
  margin-right: 190px;

  .title {
    margin-bottom: 15px;
  }

  .subtitle {
    margin-bottom: 50px;
  }

  .text {
    margin-bottom: 50px;
  }

  .submit_light {
    min-width: 282px;
  }
}

@media (max-width: $tablet) {
  .applicable__content {
    align-items: center;
    margin: 0;

    .title {
      margin-bottom: 11px;
    }

    .subtitle {
      margin-bottom: 28px;
    }

    .text {
      text-align: center;
      margin-bottom: 40px;
    }
  }
}

@media (max-width: $mobile) {
  .applicable__content {
    width: 290px;
    align-items: center;
    margin: 0;

    .title {
      text-align: center;
    }

    .subtitle {
      text-align: center;
      margin-bottom: 21px;
    }

    .text {
      text-align: center;
      margin-bottom: 29px;
    }
  }
}

.applicable__icons {
  display: flex;
  flex-wrap: wrap;
  min-width: 480px;
  max-width: 960px;
}

@media (max-width: $tablet) {
  .applicable__icons {
    justify-content: center;
    min-width: 380px;
    max-width: 760px;
  }
}

@media (max-width: $mobile) {
  .applicable__icons {
    min-width: 220px;
    max-width: 420px;
  }
}

